import localForage from "localforage";
/**
 * A class to manage key/value browser storage.
 *
 * name is a string used to identify each storage instance.
 */
export class Storage {
    constructor(name) {
        this._ready = false;
        this._store = localForage.createInstance({
            name: Storage.mainDB,
            storeName: name,
        });
        const promise = (async () => {
            await localForage.ready();
            this._ready = true;
        })();
        this.ready = () => promise;
    }
    async get(key) {
        if (this._ready)
            return await this._store.getItem(key);
    }
    ;
    async set(key, value) {
        if (this._ready)
            await this._store.setItem(key, value);
    }
    ;
    async remove(key) {
        if (this._ready)
            await this._store.removeItem(key);
    }
    ;
    async clear() {
        if (this._ready)
            await this._store.clear();
    }
    ;
    async length() {
        if (this._ready)
            return await this._store.length();
        return 0;
    }
    ;
    async keys() {
        if (this._ready)
            return await this._store.keys();
    }
    ;
    async iterate(callback) {
        if (this._ready)
            return await this._store.iterate(callback);
    }
    ;
    static async drop(name) {
        await localForage.dropInstance({
            name: Storage.mainDB,
            storeName: name,
        });
    }
    ;
}
Storage.mainDB = "basthon";
