6;\n  --btn-default-border-color: #ccc;\n  --btn-default-border-hover-color: #adadad;\n  --btn-default-active-hover-bg-color: #d4d4d4;\n  --btn-default-active-hover-border-color: #8c8c8c;\n  --menu-bg: var(--body-bg);\n  --menu-bg-hover: #e7e7e7;\n  --menu-fg: var(--btn-default-color);\n  --menu-fg-hover: var(--navbar-default-link-hover-color);\n  --code-bg-color: var(--menu-bg-hover);\n  --edit-hover-color: var(--body-bg-darken10pc);\n  --self-color: #05a;\n  --table-bg-good: rgba(0, 255, 0, 0.23);\n  --table-bg-bad: rgba(255, 0, 0, 0.23);\n}\n[data-theme=\"darcula\"] {\n  --page-backdrop-color: #3c3f41;\n  --body-bg-light: 17%;\n  --header-bg: #313335;\n  --input-prompt-color: #307fc1;\n  --output-prompt-color: #bf5b3d;\n  --output-stderr-bg-color: rgba(244, 67, 54, 0.28);\n  --output-pre-color: #bbbbbb;\n  --black: #bbbbbb;\n  --border-color: #616161;\n  --light-border-color: #505050;\n  --navbar-default-border: var(--border-color);\n  --navbar-default-bg: var(--header-bg);\n  --navbar-default-link-color: #a5a5a5;\n  --navbar-default-link-hover-color: #cbcbcb;\n  --btn-default-color: var(--navbar-default-link-color);\n  --btn-default-border-color: var(--navbar-default-border);\n  --menu-bg-hover: var(--page-backdrop-color);\n  --btn-default-bg-hover-color: var(--page-backdrop-color);\n  --btn-default-border-hover-color: var(--btn-default-border-color);\n  --btn-default-active-hover-bg-color: var(--btn-default-bg-hover-color);\n  --btn-default-active-hover-border-color: var(--btn-default-border-hover-color);\n  --edit-hover-color: #555758;\n  --rendered-html-border-color: var(--text-color);\n  --self-color: #307fc1;\n}\n.modal.fade .modal-dialog {\n  -webkit-transform: translate(0, 0);\n  -ms-transform: translate(0, 0);\n  -o-transform: translate(0, 0);\n  transform: translate(0, 0);\n}\ncode {\n  color: var(--black);\n}\npre {\n  font-size: inherit;\n  line-height: inherit;\n}\nlabel {\n  font-weight: normal;\n}\n/* Make the page background atleast 100% the height of the view port */\n/* Make the page itself atleast 70% the height of the view port */\n.border-box-sizing {\n  box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n}\n.corner-all {\n  border-radius: 2px;\n}\n.no-padding {\n  padding: 0px;\n}\n/* Flexible box model classes */\n/* Taken from Alex Russell http://infrequently.org/2009/08/css-3-progress/ */\n/* This file is a compatibility layer.  It allows the usage of flexible box \nmodel layouts accross multiple browsers, including older browsers.  The newest,\nuniversal implementation of the flexible box model is used when available (see\n`Modern browsers` comments below).  Browsers that are known to implement this \nnew spec completely include:\n\n    Firefox 28.0+\n    Chrome 29.0+\n    Internet Explorer 11+ \n    Opera 17.0+\n\nBrowsers not listed, including Safari, are supported via the styling under the\n`Old browsers` comments below.\n*/\n.hbox {\n  /* Old browsers */\n  display: -webkit-box;\n  -webkit-box-orient: horizontal;\n  -webkit-box-align: stretch;\n  display: -moz-box;\n  -moz-box-orient: horizontal;\n  -moz-box-align: stretch;\n  display: box;\n  box-orient: horizontal;\n  box-align: stretch;\n  /* Modern browsers */\n  display: flex;\n  flex-direction: row;\n  align-items: stretch;\n}\n.hbox > * {\n  /* Old browsers */\n  -webkit-box-flex: 0;\n  -moz-box-flex: 0;\n  box-flex: 0;\n  /* Modern browsers */\n  flex: none;\n}\n.vbox {\n  /* Old browsers */\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-box-align: stretch;\n  display: -moz-box;\n  -moz-box-orient: vertical;\n  -moz-box-align: stretch;\n  display: box;\n  box-orient: vertical;\n  box-align: stretch;\n  /* Modern browsers */\n  display: flex;\n  flex-direction: column;\n  align-items: stretch;\n}\n.vbox > * {\n  /* Old browsers */\n  -webkit-box-flex: 0;\n  -moz-box-flex: 0;\n  box-flex: 0;\n  /* Modern browsers */\n  flex: none;\n}\n.hbox.reverse,\n.vbox.reverse,\n.reverse {\