);\n}\n.scaleX(@ratio) {\n  -webkit-transform: scaleX(@ratio);\n      -ms-transform: scaleX(@ratio); // IE9 only\n       -o-transform: scaleX(@ratio);\n          transform: scaleX(@ratio);\n}\n.scaleY(@ratio) {\n  -webkit-transform: scaleY(@ratio);\n      -ms-transform: scaleY(@ratio); // IE9 only\n       -o-transform: scaleY(@ratio);\n          transform: scaleY(@ratio);\n}\n.skew(@x; @y) {\n  -webkit-transform: skewX(@x) skewY(@y);\n      -ms-transform: skewX(@x) skewY(@y); // See https://github.com/twbs/bootstrap/issues/4885; IE9+\n       -o-transform: skewX(@x) skewY(@y);\n          transform: skewX(@x) skewY(@y);\n}\n.translate(@x; @y) {\n  -webkit-transform: translate(@x, @y);\n      -ms-transform: translate(@x, @y); // IE9 only\n       -o-transform: translate(@x, @y);\n          transform: translate(@x, @y);\n}\n.translate3d(@x; @y; @z) {\n  -webkit-transform: translate3d(@x, @y, @z);\n          transform: translate3d(@x, @y, @z);\n}\n.rotate(@degrees) {\n  -webkit-transform: rotate(@degrees);\n      -ms-transform: rotate(@degrees); // IE9 only\n       -o-transform: rotate(@degrees);\n          transform: rotate(@degrees);\n}\n.rotateX(@degrees) {\n  -webkit-transform: rotateX(@degrees);\n      -ms-transform: rotateX(@degrees); // IE9 only\n       -o-transform: rotateX(@degrees);\n          transform: rotateX(@degrees);\n}\n.rotateY(@degrees) {\n  -webkit-transform: rotateY(@degrees);\n      -ms-transform: rotateY(@degrees); // IE9 only\n       -o-transform: rotateY(@degrees);\n          transform: rotateY(@degrees);\n}\n.perspective(@perspective) {\n  -webkit-perspective: @perspective;\n     -moz-perspective: @perspective;\n          perspective: @perspective;\n}\n.perspective-origin(@perspective) {\n  -webkit-perspective-origin: @perspective;\n     -moz-perspective-origin: @perspective;\n          perspective-origin: @perspective;\n}\n.transform-origin(@origin) {\n  -webkit-transform-origin: @origin;\n     -moz-transform-origin: @origin;\n      -ms-transform-origin: @origin; // IE9 only\n          transform-origin: @origin;\n}\n\n\n// Transitions\n\n.transition(@transition) {\n  -webkit-transition: @transition;\n       -o-transition: @transition;\n          transition: @transition;\n}\n.transition-property(@transition-property) {\n  -webkit-transition-property: @transition-property;\n          transition-property: @transition-property;\n}\n.transition-delay(@transition-delay) {\n  -webkit-transition-delay: @transition-delay;\n          transition-delay: @transition-delay;\n}\n.transition-duration(@transition-duration) {\n  -webkit-transition-duration: @transition-duration;\n          transition-duration: @transition-duration;\n}\n.transition-timing-function(@timing-function) {\n  -webkit-transition-timing-function: @timing-function;\n          transition-timing-function: @timing-function;\n}\n.transition-transform(@transition) {\n  -webkit-transition: -webkit-transform @transition;\n     -moz-transition: -moz-transform @transition;\n       -o-transition: -o-transform @transition;\n          transition: transform @transition;\n}\n\n\n// User select\n// For selecting text on the page\n\n.user-select(@select) {\n  -webkit-user-select: @select;\n     -moz-user-select: @select;\n      -ms-user-select: @select; // IE10+\n          user-select: @select;\n}\n","// Mixin CSS classes\n\n.border-box-sizing {\n    box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n}\n\n.corner-all {\n    border-radius: @border-radius-base;\n}\n\n.border-radius(@radius) {\n  border-radius: @radius;\n}\n\n.no-padding {\n    padding: 0px;\n}\n","\n/* Flexible box model classes */\n/* Taken from Alex Russell http://infrequently.org/2009/08/css-3-progress/ */\n\n/* This file is a compatibility layer.  It allows the usage of flexible box \nmodel layouts accross multiple browsers, including older browsers.  The newest,\nuniversal implementation of the flexible box model is used when available (see\n`Modern browsers` comments below).  Browsers that are known to implement this \nnew spec completely include:\n\n    Firefox 28.0+\n    Chrome 29.0+\n    Internet Explorer 11+ \n    Opera 17.0+\n\nBrowsers not listed, including Safari, are supported via the styling under the\n`Old browsers` comments below.\n*/\n\n\n.hbox {\n    /* Old browsers */\n    display: -webkit-box;\n    -webkit-box-orient: horizontal;\n    -webkit-box-align: stretch;\n\n    display: -moz-box;\n    -moz-box-orient: horizontal;\n    -moz-box-align: stretch;\n\n    display: box;\n    box-orient: horizontal;\n    box-align: stretch;\n\n    /* Modern browsers */\n    display: flex;\n    flex-direction: row;\n    align-items: stretch;\n}\n\n.hbox > * {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n\n    /* Modern browsers */\n    flex: none;\n}\n\n.vbox {\n    /* Old browsers */\n    display: -webkit-box;\n    -webkit-box-orient: vertical;\n    -webkit-box-align: stretch;\n\n    display: -moz-box;\n    -moz-box-orient: vertical;\n    -moz-box-align: stretch;\n\n    display: box;\n    box-orient: vertical;\n    box-align: stretch;\n\n    /* Modern browsers */\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n}\n\n.vbox > * {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n    \n    /* Modern browsers */\n    flex: none;\n}\n\n.hbox.reverse,\n.vbox.reverse,\n.reverse {\n    /* Old browsers */\n    -webkit-box-direction: reverse;\n    -moz-box-direction: reverse;\n    box-direction: reverse;\n    \n    /* Modern browsers */\n    flex-direction: row-reverse;\n}\n\n.hbox.box-flex0,\n.vbox.box-flex0,\n.box-flex0 {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n    \n    /* Modern browsers */\n    flex: none;\n    width: auto;\n}\n\n.hbox.box-flex1,\n.vbox.box-flex1,\n.box-flex1 {\n    /* Old browsers */\n    -webkit-box-flex: 1;\n    -moz-box-flex: 1;\n    box-flex: 1;\n\n    /* Modern browsers */\n    flex: 1;\n}\n\n.hbox.box-flex,\n.vbox.box-flex,\n.box-flex {\n    /* Old browsers */\n    .box-flex1();\n}\n\n.hbox.box-flex2,\n.vbox.box-flex2,\n.box-flex2 {\n    /* Old browsers */\n    -webkit-box-flex: 2;\n    -moz-box-flex: 2;\n    box-flex: 2;\n\n    /* Modern browsers */\n    flex: 2;\n}\n\n.box-group1 {\n    /*  Deprecated */\n    -webkit-box-flex-group: 1;\n    -moz-box-flex-group: 1;\n    box-flex-group: 1;\n}\n\n.box-group2 {\n    /* Deprecated */\n    -webkit-box-flex-group: 2;\n    -moz-box-flex-group: 2;\n    box-flex-group: 2;\n}\n\n.hbox.start, \n.vbox.start,\n.start {\n    /* Old browsers */\n    -webkit-box-pack: start;\n    -moz-box-pack: start;\n    box-pack: start;\n    \n    /* Modern browsers */\n    justify-content: flex-start;\n}\n\n.hbox.end, \n.vbox.end,\n.end {\n    /* Old browsers */\n    -webkit-box-pack: end;\n    -moz-box-pack: end;\n    box-pack: end;\n    \n    /* Modern browsers */\n    justify-content: flex-end;\n}\n\n.hbox.center, \n.vbox.center,\n.center {\n    /* Old browsers */\n    -webkit-box-pack: center;\n    -moz-box-pack: center;\n    box-pack: center;\n    \n    /* Modern browsers */\n    justify-content: center;\n}\n\n.hbox.baseline, \n.vbox.baseline,\n.baseline {\n    /* Old browsers */\n    -webkit-box-pack: baseline;\n    -moz-box-pack: baseline;\n    box-pack: baseline;\n    \n    /* Modern browsers */\n    justify-content: baseline;\n}\n\n.hbox.stretch, \n.vbox.stretch,\n.stretch {\n    /* Old browsers */\n    -webkit-box-pack: stretch;\n    -moz-box-pack: stretch;\n    box-pack: stretch;\n    \n    /* Modern browsers */\n    justify-content: stretch;\n}\n\n.hbox.align-start, \n.vbox.align-start,\n.align-start {\n    /* Old browsers */\n    -webkit-box-align: start;\n    -moz-box-align: start;\n    box-align: start;\n    \n    /* Modern browsers */\n    align-items: flex-start;\n}\n\n.hbox.align-end, \n.vbox.align-end,\n.align-end {\n    /* Old browsers */\n    -webkit-box-align: end;\n    -moz-box-align: end;\n    box-align: end;\n    \n    /* Modern browsers */\n    align-items: flex-end;\n}\n\n.hbox.align-center, \n.vbox.align-center,\n.align-center {\n    /* Old browsers */\n    -webkit-box-align: center;\n    -moz-box-align: center;\n    box-align: center;\n    \n    /* Modern browsers */\n    align-items: center;\n}\n\n.hbox.align-baseline, \n.vbox.align-baseline,\n.align-baseline {\n    /* Old browsers */\n    -webkit-box-align: baseline;\n    -moz-box-align: baseline;\n    box-align: baseline;\n    \n    /* Modern browsers */\n    align-items: baseline;\n}\n\n.hbox.align-stretch, \n.vbox.align-stretch,\n.align-stretch {\n    /* Old browsers */\n    -webkit-box-align: stretch;\n    -moz-box-align: stretch;\n    box-align: stretch;\n    \n    /* Modern browsers */\n    align-items: stretch;\n}\n","div.error {\n  margin: 2em;\n  text-align: center;\n}\n\ndiv.error > h1 {\n    font-size: 500%;\n    line-height: normal;\n}\n\ndiv.error > p {\n    font-size: 200%;\n    line-height: normal;\n}\n\ndiv.traceback-wrapper {\n    text-align: left;\n    max-width: 800px;\n    margin: auto;\n    pre.traceback {\n      max-height: 600px;\n      overflow: auto;\n    }\n}\n","/* CSS font colors for translated ANSI escape sequences */\n\n.ansicolors(@name, @normal, @intense) {\n  .ansi-@{name}-fg { color: @normal; }\n  .ansi-@{name}-bg { background-color: @normal; }\n  .ansi-@{name}-intense-fg { color: @intense; }\n  .ansi-@{name}-intense-bg { background-color: @intense; }\n}\n\n/* The color values are a mix of\n   http://www.xcolors.net/dl/baskerville-ivorylight and\n   http://www.xcolors.net/dl/euphrasia */\n\n.ansicolors(black, #3E424D, #282C36);\n.ansicolors(red, #E75C58, #B22B31);\n.ansicolors(green, #00A250, #007427);\n.ansicolors(yellow, #DDB62B, #B27D12);\n.ansicolors(blue, #208FFB, #0065CA);\n.ansicolors(magenta, #D160C4, #A03196);\n.ansicolors(cyan, #60C6C8, #258F8F);\n.ansicolors(white, #C5C1B4, #A1A6B2);\n\n.ansi-default-inverse-fg { color: #FFFFFF; }\n.ansi-default-inverse-bg { background-color: #000000; }\n\n.ansi-bold { font-weight: bold; }\n.ansi-underline { text-decoration: underline; }\n","@_cell_padding_minus_border: @cell_padding - @cell_border_width;\n\n._selected_style(@border_color) {\n    position: absolute;\n    display: block;\n    top: -1 * @cell_border_width;\n    left: -1 * @cell_border_width;\n    width: @cell_left_border_width;\n    // height: 100% + (2 * @cell_border_width);\n    height: calc(~\"100% + \"(2 * @cell_border_width));\n    content: '';\n    background: @border_color;\n}\n\n\ndiv.cell {\n    .vbox();\n    .corner-all();\n    .border-box-sizing();\n    \n    border-width: @cell_border_width;\n    border-style: solid;\n    border-color: transparent;\n\n    width: 100%;\n    padding: @_cell_padding_minus_border;\n    /* This acts as a spacer between cells, that is outside the border */\n    margin: 0px;\n    outline: none;\n    \n    position: relative;\n    overflow: visible;\n\n    &:before {\n        ._selected_style(transparent);\n    }\n\n    &.jupyter-soft-selected {\n        border-left-color: @soft_select_color;\n        border-left-width: @cell_border_width;\n        padding-left: @cell_padding - @cell_border_width;\n        border-right-color: @soft_select_color;\n        border-right-width: @cell_border_width;\n        background: @soft_select_color;\n    \n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    &.selected, &.selected.jupyter-soft-selected {\n        border-color: @border_color;\n        \n        &:before {\n            ._selected_style(@selected_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    .edit_mode &.selected {\n        border-color: @edit_mode_border_color;\n        \n        &:before {\n            ._selected_style(@edit_mode_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n}\n\n\n.prompt {\n    /* This needs to be wide enough for 3 digit prompt numbers: In[100]: */\n    min-width: 14ex;\n\n    /* This padding is tuned to match the padding on the CodeMirror editor. */\n    padding: @code_padding;\n    margin: 0px;\n    font-family: @font-family-monospace;\n    text-align: right;\n\n    /* This has to match that of the the CodeMirror class line-height below */\n    line-height: @code_line_height;\n\n    /* Don't highlight prompt number selection */\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n\n    /* Use default cursor */\n    cursor: default;\n}\n\n@media (max-width: @screen-xs-min) {\n    // prompts are in the main column on small screens,\n    // so text should be left-aligned\n    .prompt {\n        text-align: left;\n    }\n}\n\ndiv.inner_cell {\n    min-width: 0;\n    .vbox();\n    .box-flex1();\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_area {\n    border: 1px solid @light_border_color;\n    .corner-all;\n    background: @cell_background;\n    line-height: @code_line_height;\n}\n\n/* This is needed so that empty prompt areas can collapse to zero height when there\n   is no content in the output_subarea and the prompt. The main purpose of this is\n   to make sure that empty JavaScript output_subareas have no height. */\ndiv.prompt:empty {\n    padding-top: 0;\n    padding-bottom: 0;\n}\n\ndiv.unrecognized_cell {\n    // from text_cell\n    padding: 5px @_cell_padding_minus_border 5px 0px;\n    .hbox();\n\n    .inner_cell {\n        .border-radius(@border-radius-base);\n        padding: @_cell_padding_minus_border;\n        font-weight: bold;\n        color: red;\n        border: @cell_border_width solid @light_border_color;\n        background: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 8.2%));\n        // remove decoration from link\n        a {\n            color: inherit;\n            text-decoration: none;\n\n            &:hover {\n                color: inherit;\n                text-decoration: none;\n            }\n        }\n    }\n}\n@media (max-width: @screen-xs-min) {\n    // remove prompt indentation on small screens\n    div.unrecognized_cell > div.prompt {\n        display: none;\n    }\n}\n","div.code_cell {\n    /* avoid page breaking on code cells when printing */\n    @media print {\n        page-break-inside: avoid;\n    }\n}\n\n/* any special styling for code cells that are currently running goes here */\ndiv.code_cell.running {\n}\n\ndiv.input {\n    page-break-inside: avoid;\n    .hbox();\n}\n\n@media (max-width: @screen-xs-min) {\n    // move prompts above code on small screens\n    div.input {\n        .vbox();\n    }\n}\n\ndiv.prompt_container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: flex-start;\n    text-align: right;\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_prompt {\n    color: @input_prompt_color;\n    border-top: 1px solid transparent;\n}\n\ndiv.run_this_cell {\n    display: none;\n    cursor: pointer;\n    color: #333;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    padding-left: 2ex;\n    padding-right: 2ex;\n    width: 1ex;\n}\n\n// set the prompt width\ndiv.prompt {\n    min-width: 15ex;\n}\n\n@media (-moz-touch-enabled: 1), (any-pointer: coarse) {\n    div.run_this_cell {\n        display: block;\n    }\n}\n\n// The styles related to div.highlight are for nbconvert HTML output only. This works\n// because the .highlight div isn't present in the live notebook. We could put this into\n// nbconvert, but it easily falls out of sync, can't use our less variables and doesn't\n// help the basic template when paired with our CSS.\n\ndiv.input_area > div.highlight {\n    margin: @code_padding;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n\ndiv.input_area > div.highlight > pre {\n    margin: 0px;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n","/* The following gets added to the <head> if it is detected that the user has a\n * monospace font with inconsistent normal/bold/italic height.  See\n * notebookmain.js.  Such fonts will have keywords vertically offset with\n * respect to the rest of the text.  The user should select a better font.\n * See: https://github.com/ipython/ipython/issues/1503\n *\n * .CodeMirror span {\n *      vertical-align: bottom;\n * }\n */\n\n.CodeMirror {\n    line-height: @code_line_height;  /* Changed from 1em to our global default */\n    font-size: @notebook_font_size;\n    height: auto;     /* Changed to auto to autogrow */\n    background: none;  /* Changed from white to allow our bg to show through */\n}\n\n.CodeMirror-scroll {\n    /*  The CodeMirror docs are a bit fuzzy on if overflow-y should be hidden or visible.*/\n    /*  We have found that if it is visible, vertical scrollbars appear with font size changes.*/\n    overflow-y: hidden;\n    overflow-x: auto;\n}\n\n.CodeMirror-lines {\n    /* In CM2, this used to be 0.4em, but in CM3 it went to 4px. We need the em value because */\n    /* we have set a different line-height and want this to scale with that. */\n    /* Note that this should set vertical padding only, since CodeMirror assumes\n       that horizontal padding will be set on CodeMirror pre */\n    padding: @code_padding 0;\n}\n\n.CodeMirror-linenumber {\n    // This is needed to fine tune the position of the line numbers because we use the 0.4em in @code_padding\n    // spacing in various places. Fine tuned to look right.\n    padding: 0 8px 0 4px;\n}\n\n.CodeMirror-gutters {\n    // This is needed because our cell has rounded corners, otherwise the gutter area square\n    // corner cuts into the rounded cell border.\n    border-bottom-left-radius: @border-radius-base;\n    border-top-left-radius: @border-radius-base;\n}\n\n.CodeMirror pre {\n    /* In CM3 this went to 4px from 0 in CM2. This sets horizontal padding only,\n    use .CodeMirror-lines for vertical */\n    padding: 0 @code_padding;\n    border: 0;\n    .border-radius(0)\n}\n\n.CodeMirror-cursor {\n    border-left: 1.4px solid black;\n}\n\n// When zoomed out 67% and 33% on a screen of 1440 width x 900 height\n@media screen and (min-width: 2138px) and (max-width: 4319px)  {\n    .CodeMirror-cursor {\n        border-left: 2px solid black;\n    }\n}\n\n// When zoomed out less than 33%\n@media screen and (min-width: 4320px)  {\n    .CodeMirror-cursor {\n        border-left: 4px solid black;\n    }\n}\n","/*\n\nOriginal style from softwaremaniacs.org (c) Ivan Sagalaev <Maniac@SoftwareManiacs.Org>\nAdapted from GitHub theme\n\n*/\n\n@import (reference) \"highlight-refs.less\";\n\n@highlight-base: #000;\n\n.highlight-base {\n  color: @highlight-base;\n}\n\n.highlight-variable {\n  .highlight-base();\n}\n\n.highlight-variable-2 {\n  // color: lighten(@highlight-base, 10%);\n  // [basthon] self is a variable-2 and must be visible\n  color: var(--self-color);\n}\n\n.highlight-variable-3 {\n  color: lighten(@highlight-base, 20%);\n}\n\n.highlight-string {\n  color: #ba2121;\n}\n\n//fix issue #4206, overwrite .highlight-comment class, from #408080 to #007979\n.highlight-comment {\n  color: #007979;\n  font-style: italic;\n}\n\n.highlight-number {\n  color: #080;\n}\n\n.highlight-atom {\n  color: #88f;\n}\n\n.highlight-keyword {\n  color: #008000;\n  font-weight: bold;\n}\n\n.highlight-builtin {\n  color: #008000;\n}\n\n.highlight-error {\n  color: #f00;\n}\n\n.highlight-operator {\n  color: #aa22ff;\n  font-weight: bold;\n}\n\n.highlight-meta {\n  color: #aa22ff;\n}\n\n/* previously not defined, copying from default codemirror */\n.highlight-def {\n  .cm-s-default.cm-def();\n}\n.highlight-punctuation {\n  .cm-s-default.cm-punctuation();\n}\n.highlight-property {\