adding-top: 3px;\n}\n\n#ipython-main-app {\n  position: relative;\n}\n\n:is(.output_svg, .output_png, .output_html, .output_javascript.rendered_html)\n  > * {\n  margin: auto;\n}\n\n/* img are inline blocks by default */\n.output_png > img {\n  display: block;\n}\n\n.output_svg > * {\n  max-width: 100%;\n  max-height: 100%;\n}\n\ndiv.output_area .rendered_html :is(table, img, canvas) {\n  margin: auto;\n}\n\ndiv.output_area .rendered_html :is(canvas) {\n  display: block;\n}\n\n/**\n * Theme override with variables (only color stuff).\n */\n.navbar-default {\n  background-color: var(--navbar-default-bg);\n}\n\n.navbar-default .navbar-nav > li > a {\n  color: var(--navbar-default-link-color);\n}\n\n.navbar-default .navbar-nav > li > a:hover,\n.navbar-default .navbar-nav > li > a:focus {\n  color: var(--navbar-default-link-hover-color);\n  background-color: var(--menu-bg-hover);\n}\n\n.navbar-default .navbar-nav > .open > a,\n.navbar-default .navbar-nav > .open > a:hover,\n.navbar-default .navbar-nav > .open > a:focus {\n  color: var(--navbar-default-link-color);\n  background-color: var(--menu-bg-hover);\n}\n\n.btn-default,\n.btn-default:focus,\n.btn-default.focus {\n  color: var(--btn-default-color);\n  background-color: var(--btn-default-bg-color);\n  border-color: var(--btn-default-border-color);\n}\n\n.btn-default:hover,\n.btn-default:active,\n.btn-default.active,\n.open > .dropdown-toggle.btn-default {\n  color: var(--btn-default-hover-color);\n  background-color: var(--btn-default-bg-hover-color);\n  border-color: var(--btn-default-border-hover-color);\n}\n\n.btn-default:active:hover,\n.btn-default.active:hover,\n.open > .dropdown-toggle.btn-default:hover,\n.btn-default:active:focus,\n.btn-default.active:focus,\n.open > .dropdown-toggle.btn-default:focus,\n.btn-default:active.focus,\n.btn-default.active.focus,\n.open > .dropdown-toggle.btn-default.focus {\n  color: var(--btn-default-hover-color);\n  background-color: var(--btn-default-active-hover-bg-color);\n  border-color: var(--btn-default-active-hover-border-color);\n}\n\n.btn-primary,\n.btn-primary:hover,\n.btn-primary:active,\n.btn-primary:active:hover,\n.btn-primary:focus {\n  color: #fff;\n  background-color: #337ab7;\n  border-color: #2e6da4;\n}\n\n.btn-danger,\n.btn-danger:hover,\n.btn-danger:active,\n.btn-danger:active:hover,\n.btn-danger:focus {\n  color: #ffffff;\n  background-color: #df0404;\n  border-color: #df0404;\n}\n\n.btn-info,\n.btn-info:hover,\n.btn-info:active,\n.btn-info:active:hover,\n.btn-info:focus {\n  color: #fff;\n  background-color: #5bc0de;\n  border-color: #46b8da;\n}\n\n.btn-success,\n.btn-success:hover,\n.btn-success:active,\n.btn-success:active:hover,\n.btn-success:focus {\n  color: #fff;\n  background-color: #5cb85c;\n  border-color: #4cae4c;\n}\n\n.btn-warning,\n.btn-warning:hover,\n.btn-warning:active,\n.btn-warning:active:hover,\n.btn-warning:focus {\n  color: #ffffff;\n  background-color: #b46102;\n  border-color: #b46102;\n}\n\n.form-control {\n  background-color: var(--btn-default-bg-color);\n  color: var(--btn-default-color);\n  border-color: var(--btn-default-border-color);\n}\n\n.dropdown-menu {\n  background-color: var(--menu-bg);\n  color: var(--navbar-default-link-color);\n  border-color: var(--navbar-default-border);\n}\n\n.dropdown-menu > li > a {\n  color: var(--menu-fg);\n  background-color: var(--menu-bg);\n}\n\n.dropdown-submenu > a::after,\n.dropdown-submenu:hover > a::after {\n  color: var(--menu-fg);\n}\n\n.dropdown-menu > li > a:hover,\ndropdown-menu > li > a:focus {\n  color: var(--menu-fg-hover);\n  background-color: var(--menu-bg-hover);\n}\n\n.dropdown-menu .divider {\n  background-color: var(--navbar-default-border);\n}\n\n.well {\n  background-color: var(--navbar-default-bg);\n  border-color: var(--navbar-default-border);\n}\n\n.popover {\n  background-color: var(--header-bg);\n}\n\n.popover-title {\n  background-color: var(--menu-bg-hover);\n  border-bottom-color: var(--navbar-default-border);\n}\n\n.jupyter