/*!
*
* IPython base
*
*/
:root {
  --page-backdrop-color: #eee;
  --body-bg-hue: 0;
  --body-bg-saturation: 0%;
  --body-bg-light: 100%;
  --body-bg: hsl(var(--body-bg-hue), var(--body-bg-saturation), var(--body-bg-light));
  --body-bg-darken2pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 2%));
  --body-bg-darken3pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 3.2%));
  --body-bg-darken5pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 5%));
  --body-bg-darken10pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 10%));
  --body-bg-darken20pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 20%));
  --body-bg-darken30pc: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 30%));
  --header-bg: var(--body-bg);
  --rendered-html-border-color: black;
  --input-prompt-color: #303f9f;
  --output-prompt-color: #d84315;
  --output-stderr-bg-color: #fdd;
  --output-pre-color: black;
  --black: #000;
  --text-color: var(--black);
  --border-color: var(--body-bg-darken30pc);
  --light-border-color: var(--body-bg-darken20pc);
  --navbar-default-border: #e7e7e7;
  --navbar-default-bg: #f5f5f5;
  --navbar-default-link-color: #565656;
  --navbar-default-link-hover-color: #333;
  --btn-default-color: var(--navbar-default-link-hover-color);
  --btn-default-hover-color: var(--btn-default-color);
  --btn-default-bg-color: var(--header-bg);
  --btn-default-bg-hover-color: #e6e6e6;
  --btn-default-border-color: #ccc;
  --btn-default-border-hover-color: #adadad;
  --btn-default-active-hover-bg-color: #d4d4d4;
  --btn-default-active-hover-border-color: #8c8c8c;
  --menu-bg: var(--body-bg);
  --menu-bg-hover: #e7e7e7;
  --menu-fg: var(--btn-default-color);
  --menu-fg-hover: var(--navbar-default-link-hover-color);
  --code-bg-color: var(--menu-bg-hover);
  --edit-hover-color: var(--body-bg-darken10pc);
  --self-color: #05a;
  --table-bg-good: rgba(0, 255, 0, 0.23);
  --table-bg-bad: rgba(255, 0, 0, 0.23);
}
[data-theme="darcula"] {
  --page-backdrop-color: #3c3f41;
  --body-bg-light: 17%;
  --header-bg: #313335;
  --input-prompt-color: #307fc1;
  --output-prompt-color: #bf5b3d;
  --output-stderr-bg-color: rgba(244, 67, 54, 0.28);
  --output-pre-color: #bbbbbb;
  --black: #bbbbbb;
  --border-color: #616161;
  --light-border-color: #505050;
  --navbar-default-border: var(--border-color);
  --navbar-default-bg: var(--header-bg);
  --navbar-default-link-color: #a5a5a5;
  --navbar-default-link-hover-color: #cbcbcb;
  --btn-default-color: var(--navbar-default-link-color);
  --btn-default-border-color: var(--navbar-default-border);
  --menu-bg-hover: var(--page-backdrop-color);
  --btn-default-bg-hover-color: var(--page-backdrop-color);
  --btn-default-border-hover-color: var(--btn-default-border-color);
  --btn-default-active-hover-bg-color: var(--btn-default-bg-hover-color);
  --btn-default-active-hover-border-color: var(--btn-default-border-hover-color);
  --edit-hover-color: #555758;
  --rendered-html-border-color: var(--text-color);
  --self-color: #307fc1;
}
