/*properties of tooltip after "expand"*/
.bigtooltip {
  overflow: auto;
  height: 200px;
}
/*properties of tooltip before "expand"*/
.smalltooltip {
  text-overflow: ellipsis;
  overflow: hidden;
  height: 85px;
}
