.btn-danger {
  color: #ffffff;
  background-color: #df0404;
  border-color: #df0404;
}
.btn-warning {
  color: #ffffff;
  background-color: #b46102;
  border-color: #b46102;
}
.close {
  float: right;
  font-size: 19.5px;
  font-weight: bold;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=60);
  opacity: 0.6;
}
.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  filter: alpha(opacity=100);
  opacity: 1;
