  /* -webkit-focus-ring-color = '#5B9DD9' */
  outline: -webkit-focus-ring-color auto 5px;
}
.basthon-loader-root {
  background-color: var(--header-bg);
  box-shadow: 0px 0px 12px 1px rgba(87, 87, 87, 0.2);
}
