mirror */\n.highlight-def {\n  .cm-s-default.cm-def();\n}\n.highlight-punctuation {\n  .cm-s-default.cm-punctuation();\n}\n.highlight-property {\n  .cm-s-default.cm-property();\n}\n.highlight-string-2 {\n  .cm-s-default.cm-string-2();\n}\n.highlight-qualifier {\n  .cm-s-default.cm-qualifier();\n}\n.highlight-bracket {\n  .cm-s-default.cm-bracket();\n}\n.highlight-tag {\n  .cm-s-default.cm-tag();\n}\n.highlight-attribute {\n  .cm-s-default.cm-attribute();\n}\n.highlight-header {\n  .cm-s-default.cm-header();\n}\n.highlight-quote {\n  .cm-s-default.cm-quote();\n}\n.highlight-link {\n  .cm-s-default.cm-link();\n}\n\n/* apply the same style to codemirror */\n.cm-s-ipython span {\n  &.cm-keyword {\n    .highlight-keyword();\n  }\n  &.cm-atom {\n    .highlight-atom();\n  }\n  &.cm-number {\n    .highlight-number();\n  }\n  &.cm-def {\n    .highlight-def();\n  }\n  &.cm-variable {\n    .highlight-variable();\n  }\n  &.cm-punctuation {\n    .highlight-punctuation();\n  }\n  &.cm-property {\n    .highlight-property();\n  }\n  &.cm-operator {\n    .highlight-operator();\n  }\n  &.cm-variable-2 {\n    .highlight-variable-2();\n  }\n  &.cm-variable-3 {\n    .highlight-variable-3();\n  }\n  &.cm-comment {\n    .highlight-comment();\n  }\n  &.cm-string {\n    .highlight-string();\n  }\n  &.cm-string-2 {\n    .highlight-string-2();\n  }\n  &.cm-meta {\n    .highlight-meta();\n  }\n  &.cm-qualifier {\n    .highlight-qualifier();\n  }\n  &.cm-builtin {\n    .highlight-builtin();\n  }\n  &.cm-bracket {\n    .highlight-bracket();\n  }\n  &.cm-tag {\n    .highlight-tag();\n  }\n  &.cm-attribute {\n    .highlight-attribute();\n  }\n  &.cm-header {\n    .highlight-header();\n  }\n  &.cm-quote {\n    .highlight-quote();\n  }\n  &.cm-link {\n    .highlight-link();\n  }\n  &.cm-error {\n    .highlight-error();\n  }\n\n  &.cm-tab {\n    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAMCAYAAAAkuj5RAAAAAXNSR0IArs4c6QAAAGFJREFUSMft1LsRQFAQheHPowAKoACx3IgEKtaEHujDjORSgWTH/ZO