t {\n        .pull-left();\n    }\n}\n\n// Make sub menus work in BS3.\n// Credit: http://www.bootply.com/86684\n.dropdown-submenu {\n    position: relative;\n}\n\n.dropdown-submenu>.dropdown-menu {\n    top: 0;\n    left: 100%;\n    margin-top: -6px;\n    margin-left: -1px;\n}\n\n[dir=\"rtl\"] .dropdown-submenu>.dropdown-menu {\n    right:100%;\n    margin-right: -1px; \n}\n\n// arrow that indicate presence of submenu\n.dropdown-submenu:hover>.dropdown-menu {\n    display: block;\n}\n\n.dropdown-submenu>a:after {\n    .fa();\n    display: block;\n    content: @fa-var-caret-right;\n    float: right;\n    color: @dropdown-link-color;\n    margin-top: 2px;\n    margin-right: -10px;\n}\n\n[dir=\"rtl\"] .dropdown-submenu>a:after {\n    float: left;\n    content: @fa-var-caret-left;\n    margin-right : 0;\n    margin-left : -10px;\n}\n\n.dropdown-submenu:hover>a:after {\n    color: @dropdown-link-hover-color;\n}\n\n.dropdown-submenu.pull-left {\n    float: none;\n}\n\n.dropdown-submenu.pull-left>.dropdown-menu {\n    left: -100%;\n    margin-left: 10px;\n}\n\n//end submenu\n","#notification_area {\n    .pull-right();\n    z-index: 10;\n}\n\n[dir=\"rtl\"] #notification_area {\n    .pull-left();\n}\n\n.indicator_area {\n    .pull-right();\n    color: var(--navbar-default-link-color);\n    margin-left: 5px;\n    margin-right: 5px;\n    width: 11px;\n    z-index: 10;\n    text-align: center;\n    width: auto;\n}\n\n[dir=\"rtl\"] .indicator_area {\n    .pull-left();\n}\n\n#kernel_indicator {\n    .indicator_area();\n    \n    border-left: 1px solid;\n    \n    .kernel_indicator_name {\n        padding-left: 5px;\n        padding-right: 5px;\n    }\n}\n\n[dir=\"rtl\"] #kernel_indicator {\n    .pull-left();\n    border-left: 0;\n    border-right : 1px solid;\n}\n\n#modal_indicator {\n    .pull-right();\n    .indicator_area();\n}\n\n[dir=\"rtl\"] #modal_indicator {\n    .pull-left();\n} \n\n#readonly-indicator {\n    .pull-right();\n    .indicator_area();\n\n    margin-top: 2px;