import { PromiseDelegate } from "promise-delegate";
import { CheckpointsManager, Storage } from "@basthon/checkpoints";
import { KernelLoader } from "@basthon/kernel-loader";
/**
 * Base class for console and notebook GUI.
 */
export class GUIBase {
    constructor(options) {
        this._loaded = new PromiseDelegate();
        this._maxCheckpoints = 10;
        this._contentFilename = "content.txt";
        this._urlKey = "content";
        this._extensions = new Map();
        /* console errors redirected to notification system */
        this._console_error = console.error;
        this._language = options?.kernelOptions?.language;
        this._loader = new KernelLoader(options.kernelOptions);
        // loading Basthon (errors are fatal)
        this._loader.showLoader("Chargement de Basthon...", false, false);
        /* per language checkpoints */
        if (!options.noCheckpointsInit)
            this._checkpoints = new CheckpointsManager(`${options.uiName}.${this._language}`, this._maxCheckpoints);
        /* globale states (not language specific) */
        this._stateStorage = new Storage(`${options.uiName}.states`);
    }
    /**
     * Language getter.
     */
    get language() {
        return this._language;
    }
    /**
     * Kernel getter.
     */
    get kernel() {
        return this._loader.kernel;
    }
    /**
     * Get the kernel or null if it's not ready.
     */
    get kernelSafe() {
        return this._loader.kernelSafe;
    }
    /**
     * KernelLoader getter.
     */
    get kernelLoader() {
        return this._loader;
    }
    /**
     * Check if GUI is loaded.
     */
    async loaded() {
        await this._loaded.promise;
    }
    /**
     * Wait for par load (document.body available).
     */
    async pageLoad() {
        await this._loader.pageLoad();
    }
    /**
     * Set the checkpoints manager
     * (typically use with GUIOptions.noCheckpointsInit set to true).
     */
    setCheckpointsManager(checkpoints) {
        this._checkpoints = checkpoints;
    }
    /**
     * Notify the user.
     */
    info(title, message) { }
    /**
     * Notify the user with an error.
     */
    error(title, message) { }
    /**
     * Ask the user to confirm or cancel.
     */
    confirm(title, message, text, callback, textCancel, callbackCancel) { }
    /**
     * Ask the user to select a choice.
     */
    select(title, message, choices, textCancel, callbackCancel) { }
    /**
     * The error notification system.
     */
    notifyError(error) {
        this._console_error(error);
        let message = error.message;
        if (message == null)
            message = error?.reason?.message;
        if (message == null)
            message = error.toString();
        // avoid html/js injection on errors
        const encodeHTMLEntities = (text) => {
            var textArea = document.createElement("textarea");
            textArea.innerText = text;
            return textArea.innerHTML;
        };
        message = encodeHTMLEntities(message).split("\n").join("<br>");
        this.error("Erreur", `Erreur : ${message}`);
        // In case of error, force loader hiding.
        try {
            this._loader.hideLoader();
        }
        catch (e) { }
    }
    /**
     * Initialize the GUI.
     */
    async init(options = null) {
        try {
            await this._init(options);
            this._loaded.resolve();
        }
        catch (error) {
            this._loaded.reject(error);
        }
    }
    /**
     * Get the content (script or notebook content).
     */
    content() {
        return "";
    }
    /**
     * Set the content (script or notebook content).
     */
    setContent(content) { }
    /**
     * Loading the content from query string (ipynb=/script= or from=).
     */
    async loadFromQS(key) {
        const url = new URL(window.location.href);
        const from_key = "from";
        let content = null;
        if (url.searchParams.has(key)) {
            content = url.searchParams.get(key) || "";
            try {
                content = await this.inflate(content);
            }
            catch (error) {
                /* backward compatibility with non compressed param */
                if (content != null)
                    content = decodeURIComponent(content);
            }
        }
        else if (url.searchParams.has(from_key)) {
            let fileURL = url.searchParams.get(from_key);
            if (fileURL != null)
                fileURL = decodeURIComponent(fileURL);
            // empty from param gives empty content
            if (fileURL === "") {
                content = "";
            }
            else {
                try {
                    const response = await fetch(fileURL);
                    if (!response.ok)
                        throw new Error(response.statusText);
                    content = await response.text();
                }
                catch (error) {
                    throw new Error(`Le chargement de ${fileURL} a échoué : ${error?.message ?? error.toString()}`);
                }
            }
        }
        if (content != null)
            this.setContent(content);
        return content;
    }
    /**
     * Load all URL requested (and registered) extensions
     * (call submited callback).
     */
    async _loadExtensions() {
        /* get requested extensions from URL */
        const url = new URL(window.location.href);
        const extensions = url.searchParams.get("extensions")?.split(",") ?? [];
        // call callbacks for requested extensions
        const promises = [];
        for (const ext of extensions) {
            const callback = this._extensions.get(ext);
            if (callback != null)
                promises.push(callback());
            else
                console.error(`Extension '${ext}' not supported.`);
        }
        await Promise.all(promises);
    }
    /**
     * Setup the UI (typically connect events, load extensions etc..).
     */
    async setupUI(options) {
        /* wait for kernel load */
        const kernel = await this.kernelLoader.kernelAvailable();
        // this is ugly but it seems complicated to make
        // GUI available in websocket for the notebook
        // so patch it!
        const dispatchEvent = kernel.dispatchEvent.bind(this.kernel);
        kernel.dispatchEvent = async (event, data) => {
            if (event === "eval.request")
                await this.backup(false);
            dispatchEvent(event, data);
        };
        kernel.addEventListener("eval.finished", () => {
            this.validateBackup();
        });
        kernel.addEventListener("eval.error", () => {
            this.validateBackup();
        });
        /* instanciate the storage */
        await this._stateStorage.ready();
    }
    /**
     * Get state from strorage.
     */
    async getState(state, def) {
        return (await this._stateStorage.get(state)) ?? def;
    }
    /**
     * Set state in storage.
     */
    async setState(state, value) {
        await this._stateStorage.set(state, value);
    }
    /**
     * Load content at startup.
     */
    async loadInitialContent(options) {
        if (window._basthonEmptyContent)
            return;
        if ((await this.loadFromQS(this._urlKey)) != null)
            return;
        this.loadFromStorage();
    }
    /**
     * Load content from local forage.
     */
    async loadFromStorage(setContent = true) {
        const approved = await this.lastBackupValid();
        let content = null;
        if (approved == null) {
            content = null;
        }
        else if (approved) {
            content = (await this._checkpoints?.getLast());
        }
        else {
            const promise = new PromiseDelegate();
            this.confirm("Récupération", "Il semble que Basthon ait rencontré un problème à sa dernière utilisation. Que voulez-vous faire ?", "Choisir une sauvegarde", async () => {
                promise.resolve(await this.selectCheckpoint());
            }, "Annuler", () => {
                promise.resolve(null);
            });
            content = await promise.promise;
        }
        if (setContent && content != null)
            this.setContent(content);
        return content;
    }
    /**
     * Tag last backup as "approved".
     */
    async validateBackup() {
        await this._checkpoints?.tagLast("approved");
    }
    /**
     * Is last backup tagged as "approved"?
     */
    async lastBackupValid() {
        return await this._checkpoints?.lastHasTag("approved");
    }
    /**
     * Select a checkpoint to load in the script.
     */
    async selectCheckpoint() {
        const times = await this._checkpoints?.times();
        const promise = new PromiseDelegate();
        const choices = times?.map((t, i) => ({
            text: CheckpointsManager.toHumanDate(parseInt(t, 10)),
            handler: async () => {
                promise.resolve((await this._checkpoints?.getIndex(i)) ?? null);
            },
        }));
        if (choices == null)
            return null;
        if (choices.length === 0) {
            this.info("Aucune sauvegarde à restaurer", "Il n'y a aucune sauvegarde à restaurer !");
            promise.resolve(null);
        }
        else {
            this.select("Choisissez une sauvegarde", "De quelle sauvegarde souhaitez-vous reprendre ?", choices, "Annuler", () => {
                promise.resolve(null);
            });
        }
        return await promise.promise;
    }
    /**
     * Backup to checkpoints.
     */
    async backup(approved = true) {
        return await this._checkpoints?.push(this.content(), approved ? ["approved"] : []);
    }
    /**
     * Internal GUI init.
     * It calls setupUI then loadInitialContent.
     */
    async _init(options = null) {
        /* all errors redirected to notification system */
        const onerror = this.notifyError.bind(this);
        window.addEventListener("error", onerror);
        window.addEventListener("unhandledrejection", onerror);
        console.error = (message) => onerror(new Error(message));
        await this._loadExtensions();
        await this._checkpoints?.ready();
        await this.setupUI(options);
        await this.loadInitialContent(options);
        await this.kernelLoader.kernelStarted();
        const init = this.initCaller.bind(this);
        // loading aux files from URL
        await init(this.loadURLAux.bind(this), "Chargement des fichiers auxiliaires...", true);
        // loading modules from URL
        await init(this.loadURLModules.bind(this), "Chargement des modules annexes...", true);
        // end
        this.kernelLoader.hideLoader();
    }
    /**
     * Change loader text and call init function.
     * If catchError is false, in case of error, we continue the
     * init process, trying to do our best...
     */
    async initCaller(func, message, catchError) {
        this._loader.setLoaderText(message);
        try {
            return await func();
        }
        catch (error) {
            if (!catchError)
                throw error;
            this.notifyError(error);
        }
    }
    /**
     * Get mode as a string (dark/light).
     */
    async theme() {
        return;
    }
    /**
     * Restart the kernel.
     */
    kernelRestart() {
        this.kernelSafe?.resolvePendingInput();
        this.kernelSafe?.restart();
    }
    /**
     * Load ressources from URL (common part to files and modules).
     */
    async _loadFromURL(key, put) {
        const url = new URL(window.location.href);
        let promises = [];
        for (let fileURL of url.searchParams.getAll(key)) {
            fileURL = decodeURIComponent(fileURL);
            const filename = fileURL.split("/").pop() || "";
            promises.push((async () => {
                let data;
                try {
                    const response = await fetch(fileURL);
                    if (!response.ok)
                        throw new Error(response.statusText);
                    data = await response.arrayBuffer();
                    await put(filename, data);
                }
                catch (error) {
                    throw new Error(`Impossible de charger le fichier ${filename} : ${error?.message ?? error.toString()}`);
                }
            })());
        }
        await Promise.all(promises);
    }
    /**
     * Loading file in the (emulated) local filesystem (async).
     */
    async putFSRessource(file) {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsArrayBuffer(file);
            reader.onload = async (event) => {
                await this.kernelSafe?.putRessource(file.name, reader.result);
                this.info(`Fichier utilisable depuis ${this.kernel?.languageName()}`, `${file.name} est maintenant utilisable depuis ${this.kernel?.languageName()}`);
                resolve();
            };
            reader.onerror = reject;
        });
    }
    /**
     * Load auxiliary files submited via URL (aux= parameter) (async).
     */
    async loadURLAux() {
        if (this.kernelSafe == null)
            return;
        await this._loadFromURL("aux", this.kernelSafe.putFile.bind(this.kernelSafe));
    }
    /**
     * Load modules submited via URL (module= parameter) (async).
     */
    async loadURLModules() {
        if (this.kernelSafe == null)
            return;
        await this._loadFromURL("module", this.kernelSafe.putModule.bind(this.kernelSafe));
    }
    /**
     * Register an extension and its callback.
     */
    registerExtension(name, callback) {
        this._extensions.set(name, callback);
    }
    /**
     * Opening file: If it has ext as extension, loading it in
     * the editor or put on (emulated) local filesystem
     * (user is asked to), otherwise, loading it in the local
     * filesystem.
     */
    _openFile(callbacks) {
        return new Promise((resolve, reject) => {
            let input = document.createElement("input");
            input.type = "file";
            input.style.display = "none";
            input.onchange = async () => {
                if (input.files == null)
                    return;
                for (let file of input.files) {
                    const ext = file.name.split(".").pop();
                    const callback = callbacks[ext ?? ""];
                    if (callback != null) {
                        await callback(file);
                    }
                    else {
                        await this.putFSRessource(file);
                    }
                }
                resolve();
            };
            input.onerror = reject;
            document.body.appendChild(input);
            input.click();
            document.body.removeChild(input);
        });
    }
    /**
     * Open an URL in a new tab or download a file.
     */
    static openURL(url, download) {
        let anchor = document.createElement("a");
        if (download != null)
            anchor.download = download;
        anchor.href = url;
        anchor.target = "_blank";
        anchor.style.display = "none"; // just to be safe!
        document.body.appendChild(anchor);
        anchor.click();
        document.body.removeChild(anchor);
    }
    /**
     * Returning the sharing link for the content.
     */
    async sharingURL(key) {
        // to retain the Line breaks.
        let content = this.content().replace(/\r\n|\r|\n/g, "\r\n");
        const url = new URL(window.location.href);
        url.hash = "";
        url.searchParams.delete("from"); // take care of collapsing params
        try {
            content = await this.deflate(content);
        }
        catch (e) {
            // fallback
            content = encodeURIComponent(content)
                .replace(/\(/g, "%28")
                .replace(/\)/g, "%29");
        }
        url.searchParams.set(key, content);
        return url.href;
    }
    /**
     * Share content via URL.
     */
    async share() {
        const url = await this.sharingURL(this._urlKey);
        const message = `
Un lien permanant vers le contenu actuel a été créé.
<br>
<i class="fa fa-exclamation-circle"></i> Attention, partager un document trop long peut ne pas fonctionner avec certains navigateurs.`;
        this.confirm("Partager ce document", message, "Copier dans le presse-papier", () => GUIBase.copyToClipboard(url), "Tester le lien", () => GUIBase.openURL(url));
    }
    /**
     * Opening file (async) and load its content.
     */
    open(file) {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsText(file);
            reader.onload = (event) => {
                this.setContent(event?.target?.result);
                resolve();
            };
            reader.onerror = reject;
        });
    }
    /**
     * Download content to file.
     */
    download(filename) {
        this.backup();
        const content = this.content().replace(/\r\n|\r|\n/g, "\r\n"); // To retain the Line breaks.
        let blob = new Blob([content], { type: "text/plain" });
        GUIBase.openURL(window.URL.createObjectURL(blob), filename ?? this._contentFilename);
    }
    /**
     * Compress a string to another string (URL safe).
     */
    async deflate(content) {
        // dynamic import for webpack
        const pako = await import("pako");
        const { Base64 } = await import("js-base64");
        return Base64.fromUint8Array(pako.deflate(content), true);
    }
    /**
     * Reverse version of deflate.
     */
    async inflate(content) {
        // dynamic import for webpack
        const pako = await import("pako");
        const { Base64 } = await import("js-base64");
        return pako.inflate(Base64.toUint8Array(content), { to: "string" });
    }
    /**
     * Handle communication between dynamic iframe and main thread
     * (mainly for auto-resize). Iframe comes from a display event,
     * like in tutor.
     */
    async handleDynamicIFrame(iframe) {
        const { port1, port2 } = new MessageChannel();
        port1.onmessage = (event) => {
            const data = event.data;
            const type = data?.type;
            switch (type) {
                case "size":
                    const { width, height } = data?.content;
                    // accepts only numbers since this could
                    // become untrusted content in case of corrupted iframe
                    if (typeof width == "number")
                        iframe.style.width = `${width}px`;
                    if (typeof height == "number")
                        iframe.style.height = `${height}px`;
                default:
                    break;
            }
        };
        // wait for iframe to be ready
        await new Promise((resolve, reject) => {
            iframe.onload = () => resolve();
            iframe.onerror = reject;
        });
        iframe.contentWindow.postMessage(port2, "*", [port2]);
        // refresh drawing at first display
        const observer = new IntersectionObserver((entries) => {
            if (entries[0].intersectionRatio > 0) {
                port1.postMessage({ type: "refresh" });
                observer.disconnect(); // refresh only once
            }
        }, { root: document.documentElement });
        observer.observe(iframe);
    }
    /**
     * Handle communication between iframe and kernel
     * (e.g. matplotlib). Iframe comes from a display event,
     * like in matplotlib.
     */
    async handleKernelIFrame(iframe, port) {
        // wait for iframe to be ready
        await new Promise((resolve, reject) => {
            iframe.onload = () => resolve();
            iframe.onerror = reject;
        });
        iframe.contentWindow.postMessage(port, "*", [port]);
    }
}
/**
 * Copy a text to clipboard.
 */
GUIBase.copyToClipboard = function (text) {
    if (navigator.clipboard != null)
        navigator.clipboard.writeText(text);
    else {
        // using the deprecated API document.execCommand('copy')
        let textArea = document.createElement("textarea");
        // Precautions from https://stackoverflow.com/questions/400212/how-do-i-copy-to-the-clipboard-in-javascript
        // Place in top-left corner of screen regardless of scroll position.
        textArea.style.position = "fixed";
        textArea.style.top = "0px";
        textArea.style.left = "0px";
        // Ensure it has a small width and height. Setting to 1px / 1em
        // doesn't work as this gives a negative w/h on some browsers.
        textArea.style.width = "2em";
        textArea.style.height = "2em";
        // We don't need padding, reducing the size if it does flash render.
        textArea.style.padding = "0px";
        // Clean up any borders.
        textArea.style.border = "none";
        textArea.style.outline = "none";
        textArea.style.boxShadow = "none";
        // Avoid flash of white box if rendered for any reason.
        textArea.style.background = "transparent";
        textArea.value = text;
        document.body.appendChild(textArea);
        textArea.focus();
        textArea.select();
        try {
            let successful = document.execCommand("copy");
            let msg = successful ? "successful" : "unsuccessful";
            console.log("Copying text command was " + msg);
        }
        catch (err) {
            console.log("Oops, unable to copy");
        }
        document.body.removeChild(textArea);
    }
};
